import adminApi from "apis/adminApi";
import React from "react";
import { Button, Modal } from "react-bootstrap";
import { useDispatch } from "react-redux";
import { deleteUserById } from "reducers/adminSlice";

import "./ModalHandle.scss";

function ModalDeleteUserId({ userId, isShow, onShow, userEmail }) {
  const dispatch = useDispatch();

  //Delete books by id
  const deleteUserId = async () => {
    const id = userId;
    try {
      await adminApi.deleteUser(id);
      alert("Xo ngi dng hon tt");
      //reset li user
      dispatch(deleteUserById());
      onShow(false);
    } catch (error) {
      console.log("c li", { error });
    }
  };
  const handleClick = () => {
    deleteUserId();
  };

  //Thit lp cho cha
  const handleClose = () => {
    return onShow(false);
  };
  return (
    <>
      <Modal show={isShow} onHide={handleClose}>
        <Modal.Header>
          <Modal.Title>Xo ngi dng</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          Xo ngi dng c gmail l <strong> {userEmail}</strong>, bn c chc
          chn xo khng?
        </Modal.Body>
        <Modal.Footer>
          <Button variant="danger" onClick={handleClick}>
            Xo ngi dng
          </Button>
          <Button variant="secondary" onClick={handleClose}>
            Quay li
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
}

export default ModalDeleteUserId;
