import productsApi from "apis/productsApi";
import React, { useEffect, useState } from "react";
import { Button, Modal, Table } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import {
  createCarts,
  deleteCarts,
  selectCartCheckout,
} from "reducers/librarianSlice";

function ModalCheckout({ userId, isShow, onShow, update, onUpdate }) {
  const dispatch = useDispatch();

  const [book, setBook] = useState();
  const booksCart = useSelector(selectCartCheckout);

  useEffect(() => {
    setBook(booksCart);
  }, [booksCart]);

  const handleClose = () => {
    onShow(false);
  };

  const handleDelete = (bookId) => {
    dispatch(deleteCarts(bookId));
  };

  const renderBooks = book?.map((bookId, index) => {
    return (
      <tr key={index}>
        <td>{index + 1}</td>
        <td>{bookId}</td>
        <td>
          <Button onClick={() => handleDelete(bookId)}>Xo</Button>
        </td>
      </tr>
    );
  });

  const checkoutBooks = async () => {
    try {
      await productsApi.postCopiesCheckout({
        user: userId,
        copies: book,
      });
      alert("Cho mn thnh cng");
      //set li carts checkout
      dispatch(createCarts());
      onUpdate(!update);
      onShow(false);
    } catch (error) {
      console.log("li ri", { error });
    }
  };

  const handleCheckout = () => {
    // console.log("Tin hnh cho mn", book);
    if (book.length > 0) {
      checkoutBooks();
    } else {
      alert("Cha c cun no");
    }
  };

  return (
    <Modal show={isShow} onHide={handleClose}>
      <Modal.Header>
        <Modal.Title>Tin hnh cho mn sch</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Table hover bordered>
          <thead>
            <tr>
              <th>STT</th>
              <th>M sch</th>
            </tr>
          </thead>
          <tbody>{renderBooks}</tbody>
        </Table>
      </Modal.Body>
      <Modal.Footer>
        <Button onClick={handleCheckout} variant="success">
          Cho mn sch
        </Button>
        <Button onClick={handleClose} variant="secondary">
          Quay li
        </Button>
      </Modal.Footer>
    </Modal>
  );
}

export default ModalCheckout;
