import productsApi from "apis/productsApi";
import React from "react";
import { Button, Modal } from "react-bootstrap";
import { useDispatch } from "react-redux";
import { deleteBookById } from "reducers/bookSlice";

import "./ModalHandle.scss";

function ModalDeleteBookId({ isShow, bookId, onShow, bookName }) {
  //Delete books by id
  const dispatch = useDispatch();
  const deleteBookId = async () => {
    const id = bookId;
    // console.log(id);
    try {
      await productsApi.postDeleteBookById(id);
      alert("Xo thnh cng");
      dispatch(deleteBookById(id));
      // console.log("Thnh cng");

      onShow(false);
    } catch (error) {
      console.log("c li", { error });
    }
  };
  const handleClick = () => {
    // console.log(" ly c Id", bookId);
    deleteBookId();
  };

  //Thit lp cho cha
  const handleClose = () => {
    return onShow(false);
  };
  return (
    <Modal show={isShow} onHide={handleClose} dialogClassName="modal-30w">
      <Modal.Header>
        <Modal.Title>Tin hnh xo sch</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        Xo sch c tn l
        <strong> {bookName}</strong>, bn c chc chn xo khng?
      </Modal.Body>
      <Modal.Footer>
        <Button variant="danger" onClick={handleClick}>
          Xo sch
        </Button>
        <Button variant="secondary" onClick={handleClose}>
          Quay li
        </Button>
      </Modal.Footer>
    </Modal>
  );
}

export default ModalDeleteBookId;
