// dd-mm-yy
export const renderDate = (time) => {
  const date = new Date(time);
  var dd = String(date.getUTCDate()).padStart(2, "0");
  var mm = String(date.getUTCMonth() + 1).padStart(2, "0");
  //January is 0!
  var yyyy = date.getUTCFullYear();

  //time
  // var minutes = date.getUTCMinutes();
  // var hours = date.getUTCHours();
  var minutes = ("0" + date.getUTCMinutes()).slice(-2);
  var hours = ("0" + date.getUTCHours()).slice(-2);

  return dd + "/" + mm + "/" + yyyy + " lc " + hours + ":" + minutes;
};

export const renderDateNow = (time) => {
  const date = new Date(time);
  var dd = String(date.getDate()).padStart(2, "0");
  var mm = String(date.getMonth() + 1).padStart(2, "0");
  //January is 0!
  var yyyy = date.getFullYear();

  //time
  var minutes = ("0" + date.getMinutes()).slice(-2);
  var hours = ("0" + date.getHours()).slice(-2);

  return dd + "/" + mm + "/" + yyyy + " lc " + hours + ":" + minutes;
};

export const renderDateSearch = (time) => {
  const date = new Date(time);
  var dd = String(date.getUTCDate()).padStart(2, "0");
  var mm = String(date.getUTCMonth() + 1).padStart(2, "0");
  //January is 0!
  var yyyy = date.getUTCFullYear();

  return dd + "/" + mm + "/" + yyyy;
};

//Pagination
export const renderPagination = (c, m) => {
  var current = c,
    last = m,
    delta = 2,
    left = current - delta,
    right = current + delta + 1,
    range = [],
    rangeWithDots = [],
    l;

  for (let i = 1; i <= last; i++) {
    if (i === 1 || i === last || (i >= left && i < right)) {
      range.push(i);
    }
  }

  for (let i of range) {
    if (l) {
      if (i - l === 2) {
        rangeWithDots.push(l + 1);
      } else if (i - l !== 1) {
        rangeWithDots.push("...");
      }
    }
    rangeWithDots.push(i);
    l = i;
  }

  return rangeWithDots;
};

// render status
export const renderStatus = (status) => {
  switch (status) {
    //Lch hn
    case "pending":
      return <>ang hn</>;
    case "fulfilled":
      return <>Thnh cng</>;
    case "expired":
      return <>Ht hn</>;

    //Trng thi sch
    case "returned":
      return <> tr</>;
    case "available":
      return <>ang trng</>;
    case "borrowing":
      return <>ang mn</>;
    case "borrowed":
      return <>ang mn</>;
    case "canceled":
      return <> hu</>;
    default:
      return <>{status} </>;
  }
};

