import productsApi from "apis/productsApi";
import React, { useState, useEffect } from "react";
import { Button, Form, Modal } from "react-bootstrap";
import "./ModalsForm.scss";

function ModalUpdateBookById({ isShow, onShow, bookDetails }) {
  //Thit lp cho cha
  const handleClose = () => {
    return onShow(false);
  };

  //Tn sch
  const [title, setTitle] = useState(bookDetails.title);
  //S ngy mn
  const [loanPeriodDays, setLoanPeriodDays] = useState(
    bookDetails.loanPeriodDays
  );
  // //S lng
  // const [copies, setCopies] = useState("");
  //Tn tc gi
  const [authors, setAuthors] = useState(bookDetails.authors);
  //Tiu 
  const [categories, setCategories] = useState(bookDetails.categories);
  //M t
  const [description, setDescription] = useState(bookDetails.description);

  const [postImage, setPostImage] = useState();

  //X l file nh
  const handleImgPost = (e) => {
    setPostImage(e.target.files[0]);
  };

  const updateBookById = async () => {
    const bookId = bookDetails.id;

    const formData = new FormData();

    if (postImage) {
      formData.append("cover", postImage);
    }

    formData.append("title", title);
    formData.append("loanPeriodDays", loanPeriodDays);
    // formData.append("copies", copies);
    formData.append("authors", authors);
    formData.append("categories", categories);
    formData.append("description", description);
    try {
      await productsApi.updateBookById(bookId, formData);
      onShow(false);
      alert("Cp nht thng tin sch thnh cng");
    } catch (error) {
      console.log("li ri", { error });
      alert(error.response.data.message);
      onShow(false);
    }
  };

  // console.log("cp nht thng tin cho book c id: ", bookDetails);
  const handleClick = () => {
    updateBookById();
  };

  return (
    <>
      <Modal show={isShow} onHide={handleClose}>
        <Modal.Header>
          <span className="title">Thay i thng tin sch</span>
          <span className="note">(Ch nhp thng tin cn thay i)</span>
        </Modal.Header>
        <Modal.Body>
          <Form className="formMenu">
            {/* Tn sch */}
            <Form.Group className="formMenu_items">
              <Form.Label className="formMenu_items_label">Tn sch</Form.Label>
              <Form.Control
                value={title}
                type="text"
                placeholder="Nhp tn sch"
                className="formMenu_items_control"
                onChange={(e) => setTitle(e.target.value)}
              />
            </Form.Group>
            {/* nh ba */}
            <Form.Group className="formMenu_items">
              <Form.Label className="formMenu_items_label">nh ba</Form.Label>
              <Form.Control
                name="cover"
                className="formMenu_items_control"
                type="file"
                onChange={handleImgPost}
              />
            </Form.Group>
            {/* Tc gi */}
            <Form.Group className="formMenu_items">
              <Form.Label className="formMenu_items_label">Tc gi</Form.Label>
              <Form.Control
                value={authors}
                type="text"
                placeholder="Nhp tn tc gi"
                className="formMenu_items_control"
                onChange={(e) => setAuthors(e.target.value)}
              />
            </Form.Group>
            {/* Th loi */}
            <Form.Group className="formMenu_items">
              <Form.Label className="formMenu_items_label">Th loi</Form.Label>
              <Form.Control
                type="text"
                value={categories}
                placeholder="Nhp th loi sch"
                className="formMenu_items_control"
                onChange={(e) => setCategories(e.target.value)}
              />
            </Form.Group>
            {/* M t */}
            <Form.Group className="formMenu_items">
              <Form.Label className="formMenu_items_label">M t</Form.Label>
              <Form.Control
                type="text"
                value={description}
                placeholder="Nhp m t"
                className="formMenu_items_control"
                onChange={(e) => setDescription(e.target.value)}
              />
            </Form.Group>
            {/* S ngy mn */}
            <Form.Group className="formMenu_items">
              <Form.Label className="formMenu_items_label">
                S ngy mn sch
              </Form.Label>
              <Form.Control
                type="text"
                value={loanPeriodDays}
                placeholder="Nhp s ngy mn sch"
                className="formMenu_items_control"
                onChange={(e) => setLoanPeriodDays(e.target.value)}
              />
            </Form.Group>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="success" onClick={handleClick}>
            Thay i thng tin
          </Button>
          <Button variant="secondary" onClick={handleClose}>
            Quay li
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
}

export default ModalUpdateBookById;
