import productsApi from "apis/productsApi";
import React, { useEffect, useState } from "react";
import { Button, FloatingLabel, Form, Table } from "react-bootstrap";
import { useDispatch } from "react-redux";
import { addCarts } from "reducers/librarianSlice";

function CheckBooks({ userId, onHandleShow }) {
  const [bookId, setBookId] = useState("");
  //Set thng tin copies sch
  const [copies, setCopies] = useState();
  //Set thng tin id sch
  const [bookIdInfo, setBookIdInfo] = useState();

  const dispatch = useDispatch();

  // useEffect(() => {
  //   console.log(bookId);
  // }, [bookId]);

  // useEffect(() => {
  //   // console.log(" search c thng tin bookInfoId", bookIdInfo);
  //   console.log(" search c thng tin copies", copies[0].title);
  // }, [bookIdInfo, copies]);

  //Kim thng tin sch
  //Kim tra m Copies
  const getInfoCopies = async () => {
    try {
      const response = await productsApi.getCheckCopies(bookId);
      console.log(" ly ra thng tin sch", response);
      setCopies([response]);
    } catch (error) {
      console.log("li ri", { error });
      alert("Hy nhp ng m sch");
    }
  };
  //Kim tra m Id sch
  const getInfoId = async (bookId) => {
    try {
      const response = await productsApi.getBooksById(bookId);
      // console.log(" ly ra thng tin sch", response);
      setBookIdInfo(response.copies);
    } catch (error) {
      console.log("li ri", { error });
    }
  };

  //btn handle
  const handleAddCarts = (copiesId) => {
    dispatch(addCarts(copiesId));
    onHandleShow(false);
  };

  //render components
  // const renderDate = (time) => {
  //   const date = new Date(time);
  //   return date.toLocaleString();
  // };
  const checkBookById = (bookId) => {
    // console.log("Bt u tm book c id", bookId);
    getInfoId(bookId);
  };

  const renderBtnCopies = (status, copiesId, bookId, usersId) => {
    const checkUser = usersId.every((user) => user.id !== userId);
    if (checkUser) {
      if (status === "borrowed" || status === "reserved") {
        return (
          <>
            <Button disabled> c ngi t</Button>
            <Button variant="secondary" onClick={() => checkBookById(bookId)}>
              Tm kim bng ID
            </Button>
            {checkBookById(bookId)}
          </>
        );
      } else {
        return (
          <Button
            variant="success"
            type="checkbox"
            onClick={() => handleAddCarts(copiesId)}
          >
            Chn sch
          </Button>
        );
      }
    } else {
      return (
        <Button disabled variant="secondary">
          Bn  t ri
        </Button>
      );
    }
  };
  const renderBtnBookId = (status, bookId) => {
    // console.log("bookid l", bookId);
    if (status === "borrowed" || status === "reserved") {
      return (
        <>
          <Button disabled>Khng t c</Button>
        </>
      );
    } else {
      return (
        <Button
          variant="success"
          type="checkbox"
          onClick={() => dispatch(addCarts(bookId))}
        >
          Chn sch
        </Button>
      );
    }
  };
  const renderUser = (user) => {
    // console.log("user sdsad", user);
    //Nu khng c user
    if (!user) {
      return <>ang trng</>;
    }
    //Nu  c => checkId
    else {
      if (user._id === userId) {
        return <>Bn</>;
      } else if (!user._id) {
        return <></>;
      } else {
        return <>  c</>;
      }
    }
  };
  const renderStatus = (status) => {
    switch (status) {
      case "available":
        return <>ang trng</>;
      case "reserved":
        return <> t lch</>;

      default:
        return <> hu </>;
    }
  };

  // render copies books
  const renderCopies = copies?.map((copy, index) => {
    return (
      <tr key={index}>
        <td>{index + 1}</td>
        <td>{renderStatus(copy.status)}</td>
        <td>{copy.title}</td>
        {/* <td>{copy.id}</td> */}
        {/* <td>{copy.book}</td> */}
        <td>{renderUser(copy.user)}</td>
        <td>{renderBtnCopies(copy.status, copy.id, copy.book, copy.users)}</td>
      </tr>
    );
  });

  //render book by id
  const renderBookIdInfo = bookIdInfo?.map((copy, index) => {
    return (
      <tr key={index}>
        <td>{index + 1}</td>
        <td>{renderStatus(copy.status)}</td>
        <td>{copies[0].title}</td>
        <td>{renderUser(copy.user)}</td>
        <td>{renderBtnBookId(copy.status, copy._id)}</td>
      </tr>
    );
  });

  return (
    <div>
      <FloatingLabel label="Nhp m Copies ca sch">
        <Form.Control
          type="text"
          placeholder="name@example.com"
          onChange={(e) => setBookId(e.target.value)}
        />
      </FloatingLabel>
      <Button onClick={getInfoCopies}>Kim tra sch</Button>
      <div className="checkoutReservation_search_show">
        {copies && (
          <>
            <legend>Kim tra bng copies </legend>

            <Table striped hover>
              <thead>
                <tr>
                  <th>STT</th>
                  <th>Trng thi</th>
                  <th>Tn sch</th>

                  <th>Ngi mn</th>
                  {/* <th>Chn sch</th> */}
                  <th></th>
                </tr>
              </thead>
              <tbody>{renderCopies}</tbody>
            </Table>
          </>
        )}
        {bookIdInfo && (
          <>
            <legend>Kim tra bng ID </legend>
            <Table hover striped>
              <thead>
                <tr>
                  <th>STT</th>
                  <th>Trng thi</th>
                  <th>Tn sch</th>

                  <th>Ngi mn</th>
                  <th>Chn sch</th>
                </tr>
              </thead>
              <tbody>{renderBookIdInfo}</tbody>
            </Table>
          </>
        )}
      </div>
    </div>
  );
}

export default CheckBooks;
