import React from "react";
import { Modal, Button } from "react-bootstrap";

function ModalViewUserDetails({ isShow, onShow, userDetails }) {
  // console.log(userDetails);
  const handleClose = () => {
    return onShow(false);
  };

  //render components

  const countExpired = (copies) => {
    const times = copies.filter((time) => time.status === "expired");
    return times.length;
  };
  const countSuccess = (copies) => {
    const times = copies.filter((time) => time.status === "fulfilled");
    return times.length;
  };
  const countCopies = (copies) => {
    return copies.length;
  };
  const checkEmail = (status) => {
    if (status) {
      return <span>( kch hot)</span>;
    } else {
      return <span style={{ color: "red" }}>(cha kch hot)</span>;
    }
  };

  const RenderInfo = () => {
    return (
      <div className="detailsUsers">
        <div>
          <strong>Tn ngi dng: </strong>
          {userDetails.name}
        </div>
        <div>
          <strong>M ID ngi dng: </strong>
          {/* {userDetails.id} */}
          {userDetails.uid}
        </div>
        <div>
          <strong>a ch email: </strong>
          {userDetails.email}
          {checkEmail(userDetails.isEmailVerified)}
        </div>
        <div>
          <strong>Trng thi: </strong>
          {userDetails.status}
        </div>
        <div>
          <strong>Chc v: </strong>
          {userDetails.role}
        </div>
        <div>
          <strong>Tng sch ang mn: </strong>
          {countCopies(userDetails.books)} cun
        </div>
        <div>
          <strong>S ln b hn: </strong>
          {countExpired(userDetails.reservations)} ln
        </div>
        <div>
          <strong>S ln t hn thnh cng: </strong>
          {countSuccess(userDetails.reservations)} ln
        </div>
      </div>
    );
  };

  return (
    <Modal show={isShow} onHide={handleClose}>
      <Modal.Header>
        <Modal.Title>Thng tin chi tit ngi dng</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <RenderInfo />
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={handleClose}>
          ng
        </Button>
      </Modal.Footer>
    </Modal>
  );
}

export default ModalViewUserDetails;
