import productsApi from "apis/productsApi";
import { renderStatus } from "constants/RenderDate";
import React, { useEffect, useState } from "react";
import { Button, FloatingLabel, Form, Table } from "react-bootstrap";

function ReturnBooks() {
  document.title = "Tr sch";

  const [bookId, setBookId] = useState();
  const [hide, setHide] = useState(false);
  const [booksReserved, setBooksReserved] = useState();

  //sau khi check id
  useEffect(() => {
    bookId && getCheckBookCopies();
  }, [bookId]);

  //sau khi tr sch
  useEffect(() => {
    renderReservation();
  }, [booksReserved]);

  const getCheckBookCopies = async () => {
    try {
      const response = await productsApi.getCheckCopies(bookId);
      setHide(true);
      // console.log(response);
      setBooksReserved([response]);
    } catch (error) {
      alert("Hy nhp ng m Copies sch");
      console.log("li ri", { error });
    }
  };

  // tr sch
  const postReturnBook = async (bookId) => {
    try {
      await productsApi.postCopiesReturn({ copies: [bookId] });
      alert("Tr sch thnh cng");
      setBooksReserved([]);
    } catch (error) {
      console.log("li ri", { error });
      alert("Tr sch tht bi");
    }
  };

  //render component
  const setImg = (img) => {
    return <img style={{ width: "40px" }} src={img} alt="img" />;
  };
  const renderButton = (status, bookid) => {
    if (status === "borrowed") {
      return (
        <Button variant="success" onClick={() => handleReturnBooks(bookid)}>
          Tr sch
        </Button>
      );
    } else {
      return (
        <Button variant="success" disabled>
          Khng th tr
        </Button>
      );
    }
  };

  const renderCarts = booksReserved?.map((books, index) => {
    return (
      <>
        <tr key={index}>
          <td>{setImg(books.cover)}</td>
          <td>{renderStatus(books.status)}</td>
          <td>{books.user?.name}</td>
          {/* <td>{books.id}</td> */}
          <td>{books.barcode}</td>
          <td>{books.title}</td>
          <td>{books.categories}</td>
          <td>{renderButton(books.status, books.id)}</td>
        </tr>
      </>
    );
  });

  const handleReturnBooks = (bookId) => {
    // console.log("tr sch c id", bookId);
    postReturnBook(bookId);
  };

  //render form
  const renderReservation = () => {
    if (hide) {
      return (
        <Table className="tableForm" striped bordered hover>
          <thead>
            <tr>
              {/* <th>Id</th> */}
              <th>nh ba</th>
              <th>Trng thi</th>
              <th>Ngi mn</th>
              <th>M sch</th>
              <th>Tn sch</th>
              <th>Th loi</th>
              <th>Chn sch</th>
            </tr>
          </thead>
          <tbody>{renderCarts}</tbody>
        </Table>
      );
    } else {
      return <h3>Khng c g  hin, hy nhp m copies ca sch</h3>;
    }
  };

  return (
    <div className="checkoutForm">
      <legend className="form_name">Qun l tr sch ngi dng</legend>
      {/* Search thng tin user */}
      <div className="checkoutForm_input">
        <FloatingLabel label="Nhp m copies sch">
          <Form.Control
            placeholder="abcxyz"
            onChange={(e) => setBookId(e.target.value)}
          />
        </FloatingLabel>
        <Button onClick={getCheckBookCopies}>Tm kim</Button>
      </div>

      {/* Hin th ni dung */}
      {renderReservation()}
    </div>
  );
}

export default ReturnBooks;
