import React from "react";
import { Table } from "react-bootstrap";
import ReactHTMLTableToExcel from "react-html-table-to-excel";

function UserReportstoExcel({ dataChart }) {
  const renderDataUser = dataChart?.map((userDate, index) => {
    return (
      <tr>
        <td>{index + 1}</td>
        <td>{userDate.day}</td>
        <td>{userDate["Ti khon mi"]}</td>
        <td>{userDate["Ti khon b xo"]}</td>
      </tr>
    );
  });

  return (
    <div>
      <ReactHTMLTableToExcel
        id="test-table-xls-button"
        className="download-table-xls-button"
        table="tableUser"
        filename="Thng k ngi dng"
        sheet="Thng k ngi dng"
        buttonText="Xut thng k ngi dng"
      />
      <Table id="tableUser" style={{ display: "none" }}>
        <thead>
          <th>STT</th>
          <th>Ngy </th>
          <th>S lng ti khon mi</th>
          <th>S lng ti khon b xo</th>
        </thead>
        <tbody>{renderDataUser}</tbody>
      </Table>
    </div>
  );
}

export default UserReportstoExcel;
