import userApi from "apis/userApi";
import React, { useState } from "react";
import { Button, Form } from "react-bootstrap";

function ResetPassword() {
  document.title = "Thay i mt khu";

  const [password, setPassword] = useState("");
  const [passwordConfirm, setPasswordConfirm] = useState("");

  const resetPassword = async () => {
    try {
      await userApi.updateInfo({
        password: password,
      });
    } catch (error) {
      console.log("li ri", { error });
    }
  };

  const handleResetPassword = (e) => {
    e.preventDefault();
    console.log(password, passwordConfirm);
  };
  return (
    <div>
      <Form className="form" onSubmit={handleResetPassword}>
        <legend className="form_name">Thay i Mt Khu</legend>
        <Form.Group className="mb-3 form_items">
          <Form.Label className="form_items_label">
            Mt khu hin ti
          </Form.Label>
          <Form.Control
            className="form_items_input"
            name="title"
            type="text"
            placeholder="Nhp mt khu hin ti"
          />
        </Form.Group>
        <Form.Group className="mb-3 form_items">
          <Form.Label className="form_items_label">
            Nhp mt khu mi
          </Form.Label>
          <Form.Control
            className="form_items_input"
            name="title"
            type="text"
            placeholder="Nhp mt khu mi"
            onChange={(e) => setPassword(e.target.value)}
          />
        </Form.Group>
        <Form.Group className="mb-3 form_items">
          <Form.Label className="form_items_label">
            Nhp li mt khu mi
          </Form.Label>
          <Form.Control
            className="form_items_input"
            name="title"
            type="text"
            placeholder="Xc nhn ng mt khu mi"
            onChange={(e) => setPasswordConfirm(e.target.value)}
          />
        </Form.Group>
        <Button type="submit" variant="primary">
          Thay i mt khu
        </Button>
      </Form>
    </div>
  );
}

export default ResetPassword;
