import productsApi from "apis/productsApi";
import "components/Admin/Manage/AddForm.scss";
import CheckBoxAuthor from "components/customComponents/CheckBoxItems/CheckBoxAuthor";
import CheckBoxCategory from "components/customComponents/CheckBoxItems/CheckBoxCategory";
import React, { useState } from "react";
import { Button, Form, Modal } from "react-bootstrap";
import { useDispatch } from "react-redux";

function AddBooks({ isShow, onShow }) {
  // const [bookCreate, setBookCreate] = useState({});

  //Tn sch
  const [title, setTitle] = useState("");
  //S ngy mn
  const [loanPeriodDays, setLoanPeriodDays] = useState("");
  //S lng
  const [copies, setCopies] = useState("");
  //Tn tc gi
  const [authors, setAuthors] = useState("");
  //Tiu 
  const [categories, setCategories] = useState("");
  //M t
  const [description, setDescription] = useState("");
  //nh
  const [postImage, setPostImage] = useState();

  const [error, setError] = useState("");
  const createBooks = async () => {
    // const infoBookCreate = { ...bookCreate, ...postImage };
    // console.log("y l info sch mun nhp", infoBookCreate);

    const formData = new FormData();
    formData.append("cover", postImage);
    formData.append("title", title);
    formData.append("loanPeriodDays", loanPeriodDays);
    formData.append("copies", copies);
    formData.append("authors", authors);
    formData.append("categories", categories);
    formData.append("description", description);

    // console.log("y l formData", formData.entries());
    try {
      await productsApi.postCreateBook(formData);
      // const response = await productsApi.postCreateBook(formData);
      // console.log("to sch thnh cng", response);
      // setError("To sch thnh cng");
      setError("");
      alert("To thnh cng");
      //Truyn v redux
      // dispatch(addBookById(response));
      //reset li
      handleReset();
      //Tt form
      onShow(false);
    } catch (error) {
      console.log("li ri", { error });
      // setError(error.response.data.message);
      //Tt form
      onShow(false);
    }
  };

  //X l nh
  const handleImgPost = (e) => {
    setPostImage(e.target.files[0]);
  };

  const handleReset = () => {
    setCopies("");
    setDescription("");
    setLoanPeriodDays("");
    setTitle("");
    setAuthors("");
    // setPostImage("");
    setCategories("");
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    createBooks();
    // console.log(" nhn c", authors, categories);
  };

  //Thit lp cho cha
  const handleClose = () => {
    setShowAuthor(false);
    setShowCategories(false);
    onShow(false);
  };
  const handleAuthorsName = (authorName) => {
    // console.log("cha  nhn c authorname", authorName);
    setAuthors(authorName.authors);
  };
  const handleCategoriesName = (categoriesName) => {
    // console.log("cha  nhn c authorname", categoriesName);
    setCategories(categoriesName.categories);
  };

  //Dng  disabled authors, actegories
  const [showAuthor, setShowAuthor] = useState(false);
  const [showCategories, setShowCategories] = useState(false);

  return (
    <Modal show={isShow} onHide={handleClose}>
      {/* <Modal.Header>
        <Modal.Title>Nhp sch mi</Modal.Title>
      </Modal.Header> */}
      <Modal.Body>
        <Form className="form" onSubmit={handleSubmit}>
          {error && <h2>{error}</h2>}
          <legend className="form_name">To mi mt u sch</legend>
          <Form.Group className="mb-3 form_items"></Form.Group>

          {/* Tn sch */}
          <Form.Group className="mb-3 form_items">
            <Form.Label className="form_items_label">Tn sch</Form.Label>
            <Form.Control
              value={title}
              name="title"
              className="form_items_input"
              type="text"
              placeholder="Nhp tn tn sch"
              onChange={(e) => setTitle(e.target.value)}
            />
          </Form.Group>

          {/* nh ba */}
          <Form.Group className="mb-3 form_items">
            <Form.Label className="form_items_label">
              nh ba ca sch
            </Form.Label>
            <Form.Control
              // value={postImage}
              name="cover"
              className="form_items_input"
              type="file"
              onChange={handleImgPost}
            />
          </Form.Group>

          {/* Form tc gi */}

          {/* Checkbox */}
          <Form.Group className="mb-3 form_items">
            <Form.Label className="form_items_label">Tc gi</Form.Label>
            <CheckBoxAuthor
              onAuthorName={handleAuthorsName}
              status={showAuthor}
            />
          </Form.Group>

          {/* new */}
          <Form.Group className="mb-3 form_items">
            <Form.Label className="form_items_label"></Form.Label>
            <Form.Control
              type="text"
              className="form_items_newInput"
              placeholder="Nhp tn tc gi mi"
              disabled={!showAuthor}
              onChange={(e) => setAuthors(e.target.value)}
            />
            <Form.Check
              className="form_items_check"
              label="Tc gi mi"
              onClick={() => setShowAuthor(!showAuthor)}
            />
          </Form.Group>

          {/* Form th loi */}

          {/* Checkbox */}
          <Form.Group className="mb-3 form_items">
            <Form.Label className="form_items_label">Th loi</Form.Label>
            <CheckBoxCategory
              onCategoryName={handleCategoriesName}
              status={showCategories}
            />
          </Form.Group>

          {/* new */}
          <Form.Group className="mb-3 form_items">
            <Form.Label className="form_items_label"></Form.Label>
            <Form.Control
              className="form_items_newInput"
              placeholder="Nhp tn th loi mi"
              disabled={!showCategories}
              type="text"
              onChange={(e) => setCategories(e.target.value)}
            />
            <Form.Check
              className="form_items_check"
              label="Th loi mi"
              onClick={() => setShowCategories(!showCategories)}
            />
          </Form.Group>

          {/* M t */}
          <Form.Group className="mb-3 form_items">
            <Form.Label className="form_items_label">M t</Form.Label>
            <Form.Control
              value={description}
              name="description"
              className="form_items_input"
              type="text"
              placeholder="Nhp tn m t"
              onChange={(e) => setDescription(e.target.value)}
            />
          </Form.Group>

          {/* S ngy mn sch */}
          <Form.Group className="mb-3 form_items">
            <Form.Label className="form_items_label">
              S ngy mn sch
            </Form.Label>
            <Form.Control
              value={loanPeriodDays}
              name="loanPeriodDays"
              className="form_items_input"
              type="text"
              placeholder="Nhp s ngy m ngi mn c th mn sch"
              onChange={(e) => setLoanPeriodDays(e.target.value)}
            />
          </Form.Group>

          {/* S lng sch */}
          <Form.Group className="mb-3 form_items">
            <Form.Label className="form_items_label">S lng</Form.Label>
            <Form.Control
              value={copies}
              name="copies"
              className="form_items_input"
              type="text"
              placeholder="Nhp s lng"
              onChange={(e) => setCopies(e.target.value)}
            />
          </Form.Group>

          {/* Button handle */}
          <div className="btnSubmit">
            <Button type="submit" variant="primary">
              Thm sch mi
            </Button>
            <Button variant="danger" onClick={handleReset}>
              Nhp li
            </Button>
            <Button variant="secondary" onClick={handleClose}>
              Quay li
            </Button>
          </div>
        </Form>
      </Modal.Body>
      {/* <Modal.Footer></Modal.Footer> */}
    </Modal>
  );
}

export default AddBooks;
