import React, { useState } from "react";
import { Form, Button } from "react-bootstrap";
import adminApi from "apis/adminApi";

import { useSelector } from "react-redux";
import { selectUser } from "reducers/authSlice";

//Toast
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

import "components/Admin/Manage/AddForm.scss";

function CreateUser() {
  document.title = "To ngi dng mi";

  //Custom alert
  const notify = (status, info) =>
    toast[status](info, { position: toast.POSITION.BOTTOM_LEFT });

  const isRole = useSelector(selectUser);
  const checkRole = () => {
    if (isRole.role === "admin") {
      return (
        <>
          <option>Chn chc v</option>
          <option value="user">Ngi dng</option>
          <option value="librarian">Th th</option>
        </>
      );
    }
    if (isRole.role === "librarian") {
      return (
        <>
          <option>Chn chc v</option>
          <option value="user">Ngi dng</option>
        </>
      );
    }
  };

  const [userCreate, setUserCreate] = useState({
    name: "",
    role: "Chn chc v",
    email: "",
    password: "",
  });
  const [error, setError] = useState("");

  const createUser = async () => {
    const infoUser = { ...userCreate };
    try {
      await adminApi.createUser(infoUser);
      handleReset();
      // setError("To user thnh cng");
      notify("success", "To thnh cng");
    } catch (error) {
      // setError(error.response.data.message);
      notify("warn", ` Tht bi, ${error.response.data.message}`);
    }
  };

  const handleReset = () => {
    setUserCreate({ name: "", role: "Chn chc v", email: "", password: "" });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    // console.log("user thm c thng tin l", userCreate);
    createUser();
  };
  return (
    <>
      <Form className="form" onSubmit={handleSubmit}>
        <legend className="form_name">To ngi dng mi</legend>
        <Form.Group className="mb-3 form_items"></Form.Group>
        <Form.Group className="mb-3 form_items">
          <Form.Label className="form_items_label">Tn ngi dng</Form.Label>
          <Form.Control
            value={userCreate.name}
            className="form_items_input"
            name="name"
            type="text"
            placeholder="Nhp tn ngi dng"
            onChange={(e) =>
              setUserCreate({ ...userCreate, [e.target.name]: e.target.value })
            }
          />
        </Form.Group>

        <Form.Group className="mb-3 form_items">
          <Form.Label className="form_items_label">Chc v</Form.Label>
          <Form.Select
            value={userCreate.role}
            name="role"
            className="form_items_input"
            onChange={(e) =>
              setUserCreate({ ...userCreate, [e.target.name]: e.target.value })
            }
          >
            {checkRole()}
          </Form.Select>
        </Form.Group>
        <Form.Group className="mb-3 form_items">
          <Form.Label className="form_items_label">a ch email</Form.Label>
          <Form.Control
            className="form_items_input"
            value={userCreate.email}
            name="email"
            type="email"
            placeholder="Nhp a ch email"
            onChange={(e) =>
              setUserCreate({ ...userCreate, [e.target.name]: e.target.value })
            }
          />
        </Form.Group>
        <Form.Group className="mb-3 form_items">
          <Form.Label className="form_items_label">
            Mt khu ngi dng
          </Form.Label>
          <Form.Control
            value={userCreate.password}
            className="form_items_input"
            name="password"
            type="password"
            placeholder="Nhp mt khu ngi dng"
            onChange={(e) =>
              setUserCreate({ ...userCreate, [e.target.name]: e.target.value })
            }
          />
        </Form.Group>

        <div className="btnSubmit">
          <Button type="submit" variant="primary">
            To ngi dng mi
          </Button>
          <Button variant="danger" onClick={handleReset}>
            Nhp li
          </Button>
        </div>
      </Form>
      <ToastContainer />
    </>
  );
}

export default CreateUser;
