import React from "react";
import { Table } from "react-bootstrap";
import ReactHTMLTableToExcel from "react-html-table-to-excel";

function ReservationReportstoExcel({ dataChart }) {
  const renderDataReservation = dataChart?.map((reservationDate, index) => {
    return (
      <tr>
        <td>{index + 1}</td>
        <td>{reservationDate.day}</td>
        <td>{reservationDate["ang ch"]}</td>
        <td>{reservationDate["Thnh cng"]}</td>
        <td>{reservationDate["Hu hn"]}</td>
        <td>{reservationDate["Qu hn"]}</td>
      </tr>
    );
  });

  return (
    <div>
      <ReactHTMLTableToExcel
        id="test-table-xls-button"
        className="download-table-xls-button"
        table="tableReservation"
        filename="Thng k lch hn"
        sheet="Thng k lch hn"
        buttonText="Xut thng k lch hn"
      />
      <Table id="tableReservation" style={{ display: "none" }}>
        <thead>
          <th>STT</th>
          <th>Ngy </th>
          <th>S lng lch  ang ch</th>
          <th>S lng lch  thnh cng</th>
          <th>S lng lch  hu hn</th>
          <th>S lng lch  qu hn</th>
        </thead>
        <tbody>{renderDataReservation}</tbody>
      </Table>
    </div>
  );
}

export default ReservationReportstoExcel;
