import userApi from "apis/userApi";
import { renderDate, renderDateNow, renderStatus } from "constants/RenderDate";
import React, { useEffect, useState } from "react";
import { Table } from "react-bootstrap";
import { useSelector } from "react-redux";
import { selectUser } from "reducers/authSlice";

function ViewHistoryReserved() {
  document.title = "Lch s mn tr sch";

  const isUser = useSelector(selectUser);
  const [reservationInfo, setReservationInfo] = useState([]);
  useEffect(() => {
    viewHistoryReserved();
  }, [isUser]);

  const viewHistoryReserved = async () => {
    const userId = isUser.id;
    try {
      const response = await userApi.postScheduledTimes(userId);
      console.log("lch mn tr l", response);
      setReservationInfo(response.results);
    } catch (error) {
      console.log("li ri", { error });
    }
  };

  const renderReservation = () => {
    if (reservationInfo.length === 0) {
      return <span>Khng c thng tin no</span>;
    } else {
      return reservationInfo?.map((info, index) => {
        if (info.status === "borrowing") {
          return (
            <tr key={index}>
              <td>{index + 1}</td>
              <td>{renderStatus(info.status)}</td>
              <td>{info.title}</td>
              <td>{info.authors}</td>
              <td>{info.categories}</td>
              <td>{renderDate(info.dueDate)}</td>
            </tr>
          );
        }
      });
    }
  };
  const renderReturned = () => {
    if (reservationInfo.length === 0) {
      return <span>Khng c thng tin no</span>;
    } else {
      return reservationInfo?.map((info, index) => {
        if (info.status === "returned") {
          return (
            <tr key={index}>
              <td>{index + 1}</td>
              <td>{renderStatus(info.status)}</td>
              <td>{info.title}</td>
              <td>{info.authors}</td>
              <td>{info.categories}</td>
              <td>{renderDateNow(info.returnedDate)}</td>
            </tr>
          );
        }
      });
    }
  };

  return (
    <div>
      <legend className="form_name">Nhng sch ang mn</legend>
      <Table striped bordered hover>
        <thead>
          <tr>
            <th>STT</th>
            <th>Trng thi</th>
            <th>Tn sch</th>
            <th>Tn tc gi</th>
            <th>Th loi</th>
            <th>Hn tr sch</th>
          </tr>
        </thead>
        <tbody>{renderReservation()}</tbody>
      </Table>
      <legend className="form_name">Nhng sch  tr</legend>
      <Table striped bordered hover>
        <thead>
          <tr>
            <th>STT</th>
            <th>Trng thi</th>
            <th>Tn sch</th>
            <th>Tn tc gi</th>
            <th>Th loi</th>
            <th>Ngy tr sch</th>
          </tr>
        </thead>
        <tbody>{renderReturned()}</tbody>
      </Table>
    </div>
  );
}

export default ViewHistoryReserved;
