import React from "react";
import { Table } from "react-bootstrap";
import ReactHTMLTableToExcel from "react-html-table-to-excel";

function BooksReportstoExcel({ dataChart }) {
  const renderDataReservation = dataChart?.map((bookDate, index) => {
    return (
      <tr>
        <td>{index + 1}</td>
        <td>{bookDate.name}</td>
        <td>{bookDate["S lng sch mi"]}</td>
      </tr>
    );
  });

  return (
    <div>
      <ReactHTMLTableToExcel
        id="test-table-xls-button"
        className="download-table-xls-button"
        table="tableBooks"
        filename="Thng k sch"
        sheet="Thng k sch"
        buttonText="Xut thng k sch"
      />
      <Table id="tableBooks" style={{ display: "none" }}>
        <thead>
          <th>STT</th>
          <th>Ngy </th>
          <th>S lng lch sch mi</th>
        </thead>
        <tbody>{renderDataReservation}</tbody>
      </Table>
    </div>
  );
}

export default BooksReportstoExcel;
