import React, { useEffect } from "react";
import { Accordion } from "react-bootstrap";
import { Link, NavLink } from "react-router-dom";

//icon
import { AiFillFileExcel } from "react-icons/ai";
import { FaBook, FaUserCog } from "react-icons/fa";
import { IoLibrarySharp } from "react-icons/io5";
import "./DashBoard.scss";
import Logout from "components/Auth/Logout/Logout";

function LibrarianDashBoard({ url }) {
  // console.log("dashboard", url);
  useEffect(() => {
    handleShow();
  }, []);

  const handleShow = () => {
    return localStorage.getItem("showItem");
  };

  return (
    <div>
      <div className="homeMenu">
        <div className="homeMenu_link">
          <Link to="/librarian" exact={true}>
            <IoLibrarySharp className="iconItem" />
            <span className="iconName">Librarian dashboard</span>
          </Link>
        </div>
      </div>
      <Accordion
        defaultActiveKey={handleShow}
        onSelect={(e) => localStorage.setItem("showItem", e)}
      >
        {/* Qun l ngi dng */}
        <Accordion.Item eventKey="0">
          <Accordion.Header>
            <FaUserCog className="iconItem" />
            Qun L Ngi Dng
          </Accordion.Header>
          <Accordion.Body>
            <NavLink to={`${url}/users`} exact={true} activeClassName="active">
              Danh sch ngi dng
            </NavLink>
          </Accordion.Body>
          <Accordion.Body>
            <NavLink to={`${url}/users/create`} activeClassName="active">
              To ngi dng mi
            </NavLink>
          </Accordion.Body>
        </Accordion.Item>

        {/* Qun L Sch */}
        <Accordion.Item eventKey="1">
          <Accordion.Header>
            {/* <RiBookletFill className="iconItem" /> */}
            <FaBook className="iconItem" />
            Qun L Sch
          </Accordion.Header>
          <Accordion.Body>
            <NavLink
              to={`${url}/books/view`}
              exact={true}
              activeClassName="active"
            >
              Thng tin sch th vin
            </NavLink>
          </Accordion.Body>
          <Accordion.Body>
            <NavLink
              to={`${url}/books/reservations`}
              exact={true}
              activeClassName="active"
            >
              Lch hn ngi dng
            </NavLink>
          </Accordion.Body>
          <Accordion.Body>
            <NavLink
              to={`${url}/books/copies`}
              // exact={true}
              activeClassName="active"
            >
              Mn sch
            </NavLink>
          </Accordion.Body>
          <Accordion.Body>
            <NavLink
              to={`${url}/books/borrowing`}
              exact={true}
              activeClassName="active"
            >
              Lch s mn sch
            </NavLink>
          </Accordion.Body>
          <Accordion.Body>
            <NavLink
              to={`${url}/books/return`}
              exact={true}
              activeClassName="active"
            >
              Tr sch
            </NavLink>
          </Accordion.Body>
          <Accordion.Body>
            <NavLink
              to={`${url}/books/returned`}
              exact={true}
              activeClassName="active"
            >
              Lch s Tr sch
            </NavLink>
          </Accordion.Body>
        </Accordion.Item>
        {/* Qun L Bo Co */}
        <Accordion.Item eventKey="2">
          <Accordion.Header>
            <AiFillFileExcel className="iconItem" />
            Qun L Bo Co
          </Accordion.Header>
          <Accordion.Body>
            <NavLink to={`${url}/report`} activeClassName="active" exact={true}>
              Thng k hot ng
            </NavLink>
          </Accordion.Body>
          <Accordion.Body>
            <NavLink to={`${url}/report/activities`} activeClassName="active">
              Hot ng th th
            </NavLink>
          </Accordion.Body>
        </Accordion.Item>
      </Accordion>
      <div className="logout">
        <Logout />
      </div>
    </div>
  );
}

export default LibrarianDashBoard;
