import React from "react";
import productsApi from "apis/productsApi";
import { Modal, Button } from "react-bootstrap";

import "./ModalHandle.scss";

function ModalIncreaseBooks({ isShow, onShow, bookName, bookId }) {
  const handleClose = () => {
    return onShow(false);
  };

  const handleIncreaseBook = async () => {
    try {
      await productsApi.postCopies(bookId);
      onShow(false);
      alert("Thm sch thnh cng");
    } catch (error) {
      console.log("li ri");
    }
  };

  return (
    <Modal show={isShow} onHide={handleClose}>
      <Modal.Header>
        <Modal.Title>Tng s lng</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        Xc nhn s lng cho <strong> {bookName}</strong>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="primary" onClick={handleIncreaseBook}>
          Tng s lng
        </Button>
        <Button variant="secondary" onClick={handleClose}>
          Quay li
        </Button>
      </Modal.Footer>
    </Modal>
  );
}

export default ModalIncreaseBooks;
